import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'

Vue.use(Router)

export default new Router({
    //mode: "history",
    routes: [{
            path: '/',
            name: 'login',
            component: resolve => require(['@/components/login/login.vue'], resolve)
        },
        {
            path: "/select",
            name: "select",
            component: resolve => require(["@/components/publicComponents/Select.vue"], resolve)
        },
        {
            path: "/linkage",
            name: "linkage",
            component: resolve => require(["@/components/publicComponents/Linkage.vue"], resolve)
        },

        {
            path: "/datepicker",
            name: "datepicker",
            component: resolve => require(["@/components/publicComponents/DatePicker.vue"], resolve)
        },
        {
            path: "/city",
            name: "selectCity",
            component: resolve => require(["@/components/publicComponents/SelectCity/SelectCity.vue"], resolve)
        },
        {
            path: '*',
            name: 'Hello',
            component: Hello
        }
    ]
})


