const mutations = {
    INCREMENT(state, msg) {
        state.msg = msg.msg
        state.username = msg.name
        state.show = true;
        alert(1)
    },
    newName(state, msg) {
        state.userName = msg
    },
    SET_LOADING(state, value) {
        state.isLoading = value
        console.log(state)
        console.log("value=" + value)
    },
    SET_SELF(state, value) {
        state.selfinfo = value
    }
}

module.exports = mutations


