/*
get方式：
接口：http://192.168.0.241:88/test/Home/index/Test_GET 

参数：username，password
post方式：
接口：http://192.168.0.241:88/test/Home/index/Test_POST 


参数：username，password

*/

import axios from "axios"
import qs from "qs"
const testUrl = "http://127.0.0.1:3333/"
const testCount = 1;
const url = "http://192.168.0.241:3333"
const count = 0
var getUrl = ""
if (testUrl && testCount == 1) {
    getUrl = testUrl
} else if (url && count == 1) {
    getUrl = url
}
export default {
    install(Vue) {
        Vue.prototype.httpGetService = function(url, option) {
            alert(getUrl)
            if (option) {
                return axios.get(`${getUrl}${url}?` + option).then(data => data).catch(error => error)
            } else {
                return axios.get(`${getUrl}${url}`).then(data => data).catch(error => error)
            }
        }
        Vue.prototype.httpPostService = function(url, option) {
            var subData = qs.stringify(option);
            return axios.post(`${getUrl}${url}`, subData).then(data => data).catch(error => error)
        }
    }
}


