<template>
  <section>
<input @click="selectAll" type="checkbox"   name="controlAll" style="controlAll" id="controlAll"/>全选<br>
1:<input type="checkbox" name="selected" value="1" @click="select($event)" /><br>
2:<input type="checkbox" name="selected" value="2" @click="select($event)" /><br>
3:<input type="checkbox" name="selected" value="3" @click="select($event)" /><br>
4:<input type="checkbox" name="selected" value="4" @click="select($event)" /><br>
5:<input type="checkbox" name="selected" value="5" @click="select($event)" /><br>
6:<input type="checkbox" name="selected" value="6" @click="select($event)" /><br>
  </section>
</template>

<script>
export default {
  data(){
    return {
      banner:[],
      selectValue:[],
    }
  },
  methods:{
    selectAll(){
        var checklist = document.getElementsByName ("selected");
        if(document.getElementById("controlAll").checked){
          this.selectValue=[];
            for(var i=0;i<checklist.length;i++){
              checklist[i].checked = 1;
              this.selectValue.push( checklist[i].value );
            } 
        }else{
          for(var j=0;j<checklist.length;j++){
            checklist[j].checked = 0;
            this.selectValue=[];
          }
        }
        console.log( this.selectValue )
      },
      select(event){
        if( event.target.checked ){
          (this.selectValue.indexOf( event.target.value )<0) ? this.selectValue.push( event.target.value ) : "";
          console.log( this.selectValue )
        }
      }
  }
}
</script>




