//时间转换
function formatTen(num) {
    return num > 9 ? (num + "") : ("0" + num);
}
export function formatDate(date, hs = false, sp = "", isSp = true) {
    // 是否显示小时、秒  hs
    // 时间分割符 sp
    //没有分割符 hsSp
    var year = date.getFullYear();
    var month = date.getMonth() + 1;
    var day = date.getDate();
    var hour = date.getHours();
    var minute = date.getMinutes();
    var second = date.getSeconds();
    if (hs) { //显示小时
        if (isSp) { //没有分割符
            return year + formatTen(month) + formatTen(day) + formatTen(hour) + formatTen(minute) + formatTen(second);
        } else {
            return year + sp + formatTen(month) + sp + formatTen(day) + " " + formatTen(hour) + ":" + formatTen(minute) + ":" + formatTen(second);
        }

    } else {
        if (isSp) { //没有分割符
            return year + formatTen(month) + formatTen(day);
        } else {
            return year + sp + formatTen(month) + sp + formatTen(day);
        }
    }
} //时间转换

// cookie start
export function getCookie(names) {
    if (names !== "undefined") {
        var search = names + "=";
        if (document.cookie.length > 0) {
            let offset = document.cookie.indexOf(search);
            if (offset != -1) {
                offset += search.length;
                let end = document.cookie.indexOf(";", offset);
                if (end == -1)
                    end = document.cookie.length
                return unescape(document.cookie.substring(offset, end));
            } else
                return "";
        }
    } else {
        return "";
    }

} // getCookie end

export function setCookie(name, value, expires) { //value传"to24",expires传"当天23:59:59的time值"实现cookie失效时间为当天的23:59:59
    let LargeExpDate;
    if (value == "to24") {
        LargeExpDate = new Date();
        LargeExpDate.setTime(expires);
    } else {
        if (expires != null || expires > 0) {
            LargeExpDate = new Date();
            LargeExpDate.setTime(LargeExpDate.getTime() + expires * (24 * 60 * 60 * 1000));
        }
    }
    (value != "undefined" && value != undefined && value !== "") ? (document.cookie = name + "=" + escape(value) + "; path=/;" + ((expires == null) ? "" : ("expires=" + LargeExpDate.toGMTString()))) : "";
} // setCookie end

export function clearCookie(name) {
    this.setCookie(name, "", -1);
} // clearCookie end

export function setStorage(key, value) {
    if (value && value !== "undefined") {
        value = JSON.stringify(value);
        localStorage.setItem(key, value);
    } else {
        return false;
    }

} // setStorage end

export function getStorage(key) {
    var value = localStorage.getItem(key);
    if (value) {
        value = JSON.parse(value);
    }
    return value
} // getStorage end

// cookie end

export function launchFullScreen(element) {
    if (element.requestFullscreen) {
        element.requestFullscreen()
        alert(0)
    } else if (element.mozRequestFullScreen) {
        element.mozRequestFullScreen()
        alert(1)
    } else if (element.webkitRequestFullScreen) {
        element.webkitRequestFullScreen()
        alert(2)
    } else if (element.msRequestFullScreen) {
        element.msRequestFullScreen()
        alert(3)
    }

}


