<template>
  <section class="login-container">
    <div class="login-box">
      <div class="inner-box">
        <div class="login-left"></div>
        <div class="login-right">
          <div class="logo"></div>
          <div class="user">
             <div class="user-box"> 用户：<input class="" type="text" placeholder="请输入用户名" v-model="username" /> </div>
              <div class="user-box"> 密码：<input class="pwd" type="password" placeholder="请输入密码" v-model="userpwd" @keyup.enter="login"  /> </div>
               <div class="user-box"> 手机登录：<input class="pwd" type="number" placeholder="请输入手机号码" v-model="tel" @keyup.enter="login"  /> </div>

              <div class="user-box login">  <button class="" @click="login" >登陆</button> <!-- <button  class="button" @click="reset">重置</button>  --> </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import {getCookie,setCookie,setStorage,getStorage} from "../../service/public.js"
import hello from "../Hello"
import {mapActions} from "vuex"
export default {
  data(){
    return{
      username:"",
      userpwd:"",
      tel:13588888888,
    }
  },
  methods:{
    mapActions:["selfIformation"],
    login(){
      // this.$store.state.selfinfo=this.username;
      this.$store.dispatch("selfIformation", this.username )
      setCookie("user",this.username)
      setCookie("pwd",this.userpwd)
      setStorage("user",this.username)
      setStorage("pwd",this.userpwd)
      var telreg = /^(((13[0-9]{1})|(15[0-9]{1})|(17[0-9]{1})|(18[0-9]{1}))+\d{8})$/ ;
      if (!telreg.test(this.tel)) {
          this.istelerror = true
          this.checktext += " 请输入正确的手机号"
      }
      telreg.test(this.tel) ? this.$router.push("hello") :"";
     //  this.httpPostService("login",{"username":this.username,"userpwd":this.userpwd}).then(data=>{
       /* this.httpPostService("login" , {"username":this.username,"userpwd":this.userpwd} ).then(data=>{
        console.log(data)
          if(data.data.msg=="success" || telreg.test(this.tel)) {
             setCookie("user",this.username)
             setCookie("pwd",this.userpwd)
             this.$router.push("hello") 
          }else{
            alert("帐号密码不匹配")
          }
          
         // data.data
      }).catch(er=>{
        console.log(er)
      })*/
    }

  },
  mounted:function(){
    // 页面加载结束判断用户是否登陆过，登陆过自动登陆
    if( getCookie("logins")!="" ){
        (this.userpwd=getCookie("pwd") )
    }else{

    }
    getCookie("user")!="" ? (this.username=getCookie("user") ) : console.log( "getcookie err1" ) ;
    getCookie("pwd")!="" ? (this.userpwd=getCookie("pwd") ) : console.log( "getcookie err2" ) ;
    getCookie("logins")!="" ? (this.userpwd=getCookie("pwd") ) : console.log( "getcookie err3" ) 

  }
}
</script>

<style lang="less" scoped>
@import "../../publicstyle/base.less";
.login-box{
  width: 660px;
  height: 340px;
  margin: auto;
  padding-top: 20px;
  background-image: url("../../../static/img/login/loginbg.png");
  background-position: 0px;
  background-repeat: no-repeat;
  background-size: 100%;
  .inner-box{
    background-color: #fff;
    width: 630px;
    height: 300px;
    margin: auto;
    border-radius: 4px;
  }// inner-box
  .login-left{
    width: 276px;
    height: 238px;
    margin: 40px 0px 0px 40px;
    background-image: url("../../../static/img/login/loginleft.png");
    background-repeat: no-repeat;
    background-position: 0px 0px;
    background-size: 100%;
    display: inline-block;
    float: left;
  }// login-left
  .login-right{
    display:inline-block;
    width: 280px;
    height: 238px;
    float: left;
    margin-top: 47px;
    margin-left: 20px;
    .logo{
      width: 100%;
      height: 40px;
      background-image: url("../../../static/img/login/logo.png");
      background-repeat: no-repeat;
      background-position: 0px 0px;
      background-size: 100%;
    }
    .user{
      margin-top: 53px;
      .user-box{
        height: 30px;
        line-height: 30px;
        >input{
          border: 1px solid #E9E9E9;
          height: 30px;
          padding-left: 5px;
          line-height: 30px;
        }//input
        .pwd{
          margin-top: 17px;
        }// pwd
      }//user-box
       .user-box.login{
          >button{
            margin-top: 29px;
            width: 150px;
            height: 30px;
            background-color: #FBA422;
            border-radius: 3px;
            border:2px solid #E5851D;
            margin-left: 60px;
          }
        }
    }//user
  }//login-right
}
    .login-container{
      width:100%;
      height: 100%;
      margin:0px auto; 
      background-image: url("../../../static/img/login/login.jpg");
      background-repeat: no-repeat;
      background-position: 0px 0px;
      background-size: 100%;
      padding-top:350px;
    }
</style>





