<template>
  <section>
    <ul>
        <li v-for="item of firstDirectoryData"> 
          <label for="value">{{ item.first }}</label>
          <input type="checkbox" name="sex" id="value" :value=item.values  @click="firstValue($event)" />
          <ul v-if=" item.second " v-show="showSecond">
              <li v-for="seconds of item.second"> 
                  <label for="value">{{ seconds.second }}</label>
                  <input type="checkbox" name="sex" id="value" :value=seconds.values />
              </li>
          </ul>
        </li>
    </ul>
  </section>
</template>

<script>
export default {
  data(){
    return {
      firstDirectoryData:[],
      selectValues:[],
      showSecond:false,
    }
  },
  methods:{
    firstValue(event){
       if( this.selectValues.indexOf( event.target.value ) <0 ) {
          this.selectValues.push( event.target.value )
       }else{
          for(let i=0;i<this.selectValues.length;i++){
              ( this.selectValues[i].indexOf( event.target.value ) !=-1 ) ?  this.selectValues.splice(i,1) : "" ;
          }
       }
       console.log( this.selectValues );
       console.log( event )
       this.showSecond=!this.showSecond
    }
  },
  mounted:function(){
    this.firstDirectoryData=[
      {
        first:"一级",
        values:"first",
        second:{
          second:"二级",
          values:"second"
        }
      },
    
    ]
  }
}
</script>




