//  通过 actions 中定义的方法触发 commit 


const actions = {
    increment(context) {
        alert(1)
        context.commit('INCREMENT')
    },
    setLoading({ commit }, value) {

        commit('SET_LOADING', value)
    },
    selfIformation({ commit }, username) {
        commit("SET_SELF", username)
    }

}
module.exports = actions


