<template>
  <section>
    <progress max="100" :value="values"></progress>
    <div>
    用户名：<input type="text" v-model="username" @input="edit" />
    密码：<input type="password" v-model="usepwd" />
    <button @click="submitData">登录</button>
    </div>
   <input v-model="message" />
   <p> {{ message }} </p>
   <button @click="add">state</button>

  <input :value="this.$store.state.statedata" />
  <input :value="getstate" />
  <button @click="goBanner"> select </button>
  <div @click="goLogin"> login </div>
  
  <ul>
    <li v-for="item of arrdata"> {{ item }} </li>
  </ul>

 <button @click="INCREMENT"> mutations </button>

<input type="file" name="pics"  @change="upload($event)" multiple="multiple" />
 <button @click="imgUpload">upload</button>

<form action="http://127.0.0.1:3333/upload" method="post" enctype="multipart/form-data">
    <h2>单图上传</h2>
    <input type="file" name="pics">
    <input type="submit" value="提交">
</form>
<button @click="goLinkage"> linkage </button>
<button @click="godate"> datepicker </button>
<button @click="gocity"> city </button>
<button @click="getState"> state </button>
<input type="text" v-model="newstate" />
<p> {{ this.$store.state.selfinfo }} </p>


  </section>
</template>

<script>
import {launchFullScreen} from '../service/public'
// vuex 
import { mapGetters, mapActions,mapMutations } from 'vuex'

import login from "./login/login"
import selectComponents from "./publicComponents/Select"
import linkage from "./publicComponents/Linkage"
import datepicker from "./publicComponents/DatePicker"
import city from "./publicComponents/SelectCity/SelectCity"

import axios from "axios"
export default {
  components:{
    login,selectComponents
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      values:0,
      dateTime:"object",
      username:"",
      usepwd:"",
      message:"",
      status:true,
      getstate:1,
      loginName:"",
      arrdata:[],
      imgUrl:"",
      newstate:"",
    }
  },
  methods:{
    //mapActions:[this.$store.increment],
    ...mapMutations(["INCREMENT"]),
    upload(e){
        console.log( e.target.value  )
        // this.imgUrl= e.target.value  
        this.imgUrl= e.target.value
        
    },
    getState(){
      console.log( "state="+this.$store.state.selfinfo )
      this.$store.dispatch( "selfIformation", this.newstate )
    },
    imgUpload(){
      let param = new FormData(); //创建form对象
      param.append('pics', this.imgUrl);//通过append向form对象添加数据
      let header = {
			headers:{'Content-Type':'multipart/form-data'}
		}

    // {"pics":this.imgUrl}
    axios.post("http://127.0.0.1:3333/busboyload",param,header).then(data=>{
      console.log(data)
    }).catch(er=>{
      console.log(er)
    })
     /* this.httpPostService("busboyload",{"pics":this.imgUrl}).then(data=>{
        console.log(data)
      }).catch(er=>{
        console.log(er)
      })
      */
    },
    startInterval(){
      let self=this
        let dateTime=setInterval(function(){
           self.values += 10
           if( self.values>=100 ){
             clearInterval( dateTime ) 
            }
        },1000)
    },
    submitData(){
      this.httpPostService("login",{"username":this.username,"userpwd":this.usepwd}).then(data=>{
          console.log(data)
      }).catch(er=>{
        console.log(er)
      })
    },
    add(){
      this.status=!this.status
      let msg={
        msg:"success",
        name:this.message
      }
      this.$store.dispatch('setLoading', true)
    },
    goLogin(){
       //this.$router.push("login")
       this.loginName=this.username
       this.$store.commit("newName",this.username)
       this.arrdata=this.$store.getters.newArray
       this.addnewdata()
    },
    addnewdata(){
      this.$store.state.data.push(4,5)
    },
    newdata(){
    },
    edit(){
      this.goLogin()
    },
    goBanner(){
      this.$router.push("selectComponents")
    },
    goLinkage(){
        this.$router.push("linkage")
    },
    godate(){
      this.$router.push("datepicker")
    },
    gocity(){
      this.$router.push("city")
    },
    getName(){
        this.loginName=this.username
    },

  },
  mounted:function(){
    this.startInterval()
  console.log( this.$store.state.show )
  this.status=this.$store.state.show




  }
}

</script>

 


