import Vue from 'vue'
import vuex from 'vuex'
Vue.use(vuex);
import state from "./state.js"
import actions from "./action.js"
import mutations from "./mutation.js"
import getters from "./getter.js"
export default new vuex.Store({
    state,
    actions,
    mutations,
    getters,
    modules: {
        //state: state
    }
})


